/***/ (function(module, exports) {

	module.exports = {"data":{"site":{"siteMetadata":{"title":"Sanjeev Kumar Pandit","subtitle":"#FullStackWebDeveloper","copyright":"© All rights reserved.","author":{"name":"Sanjeev Kumar Pandit","twitter":"sanjeevkpandit"},"disqusShortname":"sanjeevkpanditcomnp","url":"http://sanjeevkpandit.com.np"}},"markdownRemark":{"id":"/run/media/sanjeev/Code/Projects/Personal/sanjeevkpandit.github.io/src/pages/articles/2018-04-14---Install-arch-linux/index.md absPath of file >>> MarkdownRemark","html":"<p>It’s been a long time since I have been using Arch Linux and loving it since because of its flexibility. In the mean time, I have installed the same OS for many times because I tried new things and crashed a lot of installed things. Literally, thinking about the time to repair, I just re-installed the OS. It just feels quick and easy to clear out all your garbage. However, it’s a pain to setup all those applications and configuration. That’s why I am writing this where I can find the steps I have been following to install and setup my machine my way :smile:.</p>\n<p>Okay, let’s start with the installation. Previously, I tried to run a live Ubuntu OS and then install either by the bootstrap image or using the LiveCD Image. But, nowadays, it seems the installing from live arch USB seems to be the easiest and quickest as it skips a lot of steps.</p>\n<ol>\n<li>\n<p>Connect to the internet</p>\n<ul>\n<li>Use ethernet or run <code class=\"language-text\">wifi-menu</code> command to connect to a WiFi Connection.</li>\n<li>\n<p>Check connection:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-console\"><code class=\"language-console\"># ping archlinux.org</code></pre>\n      </div>\n</li>\n</ul>\n</li>\n<li>\n<p>Partition the disks</p>\n<ul>\n<li>\n<p>The partitions can be listed from:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-console\"><code class=\"language-console\"># fdisk -l</code></pre>\n      </div>\n</li>\n<li>Usually, I prefer to prepare the partition before the installation. Still, you can find the steps <a target=\"_blank\" href=\"https://wiki.archlinux.org/index.php/installation_guide#Partition_the_disks\">here</a> to perform the partition actions.</li>\n<li>\n<p>Format the partition</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-console\"><code class=\"language-console\"># mkfs.ext4 /dev/sda1</code></pre>\n      </div>\n</li>\n</ul>\n</li>\n<li>\n<p>Mount the file system</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-console\"><code class=\"language-console\"># mount /dev/sda1 /mnt</code></pre>\n      </div>\n</li>\n<li>\n<p>Installation</p>\n<ul>\n<li>\n<p>Select and rank the mirros, ranking the mirrors would help to perform the installation quicker.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-console\"><code class=\"language-console\">// Backup\n# cp /etc/pacman.d/mirrorlist /etc/pacman.d/mirrorlist.backup\n// Uncomment every mirror\n# sed -i &#39;s/^#Server/Server/&#39; /etc/pacman.d/mirrorlist.backup\n// Rank mirrors\n# rankmirrors /etc/pacman.d/mirrorlist.backup &gt; /etc/pacman.d/mirrorlist</code></pre>\n      </div>\n</li>\n<li>\n<p>Install the base packages:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-console\"><code class=\"language-console\"># pacstrap /mnt base base-devel</code></pre>\n      </div>\n<p><code class=\"language-text\">base-devel</code> is optional, can be installed later.</p>\n</li>\n</ul>\n</li>\n<li>\n<p>Configure the system</p>\n<ul>\n<li>\n<p>Fstab</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-console\"><code class=\"language-console\"># genfstab -U /mnt &gt;&gt; /mnt/etc/fstab</code></pre>\n      </div>\n</li>\n<li>\n<p>Chroot</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-console\"><code class=\"language-console\"># arch-chroot /mnt</code></pre>\n      </div>\n</li>\n<li>\n<p>Time zone</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-console\"><code class=\"language-console\"># ln -sf /usr/share/zoneinfo/Region/City /etc/localtime\n# hwclock --systohc</code></pre>\n      </div>\n</li>\n<li>\n<p>Locale\nUncomment <code class=\"language-text\">en_US.UTF-8 UTF-8</code> and other needed localizations in <code class=\"language-text\">/etc/locale.gen</code>, and generate them with:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-console\"><code class=\"language-console\"># locale-gen</code></pre>\n      </div>\n<p>Set the LANG variable in <code class=\"language-text\">/etc/locale.conf</code> accordingly:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-console\"><code class=\"language-console\">LANG=en_US.UTF-8</code></pre>\n      </div>\n<p>To set the keyboard layout, make the changes persistent in <code class=\"language-text\">/etc/vconsole.conf</code>:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-console\"><code class=\"language-console\">KEYMAP=us</code></pre>\n      </div>\n</li>\n<li>\n<p>Hostname\nCreate the <code class=\"language-text\">hostname</code> file <code class=\"language-text\">/etc/hostname</code>:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-console\"><code class=\"language-console\">myhostname</code></pre>\n      </div>\n<p>Add matching entries to <code class=\"language-text\">/etc/hosts</code>:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-console\"><code class=\"language-console\">127.0.0.1    localhost\n::1          localhost\n127.0.1.1    myhostname.localdomain\tmyhostname</code></pre>\n      </div>\n</li>\n<li>\n<p>Network configuration</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-console\"><code class=\"language-console\"># pacman -S networkmanager dhclient</code></pre>\n      </div>\n<p>For wifi:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-console\"><code class=\"language-console\"># pacman -S iw wpa_supplicant dialog</code></pre>\n      </div>\n</li>\n<li>\n<p>Initramfs</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-console\"><code class=\"language-console\"># mkinitcpio -p linux</code></pre>\n      </div>\n</li>\n<li>\n<p>Root password</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-console\"><code class=\"language-console\"># passwd</code></pre>\n      </div>\n</li>\n<li>\n<p>Boot loader (GRUB - UEFI)\nInstall the package <code class=\"language-text\">grub</code> and <code class=\"language-text\">efibootmgr</code>:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-console\"><code class=\"language-console\"># pacman -S grub efibootmgr</code></pre>\n      </div>\n<p>Mount EFI partition(/dev/sda3):</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-console\"><code class=\"language-console\"># mkir /boot/efi\n# mount /dev/sda3 /boot/efi</code></pre>\n      </div>\n<p>Install GRUB UEFI to <code class=\"language-text\">/boot/efi</code>:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-console\"><code class=\"language-console\"># grub-install --target=x86_64-efi --efi-directory=/boot/efi --bootloader-id=arch_grub</code></pre>\n      </div>\n<p>Generate the main configuration file:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-console\"><code class=\"language-console\"># grub-mkconfig -o /boot/grub/grub.cfg</code></pre>\n      </div>\n</li>\n</ul>\n</li>\n<li>\n<p>Reboot, enjoy.</p>\n</li>\n</ol>\n<h4>Reference:</h4>\n<ul>\n<li><a href=\"https://wiki.archlinux.org/index.php/installation_guide\" target=\"_blank\">Installation guide - Arch</a></li>\n</ul>","fields":{"tagSlugs":["/tags/arch/","/tags/arch-linux/","/tags/linux/","/tags/setup/","/tags/installation/"]},"frontmatter":{"title":"Install Arch Linux","tags":["arch","arch linux","linux","setup","installation"],"date":"2018-04-14T00:00:00Z","description":"Learn how to install arch linux."}}},"pathContext":{"slug":"/posts/install-arch-linux/"}}

/***/ })

